
.spread-parent {
    display: flex;
    flex-wrap: wrap;
  }

  .spread h3,.spread h3 {
    display:block;
  }
  
  .spread {
      display:block;
    /* min-height: 800px; */
    /* min-width: calc(100% - 50px); */
    min-width:100%;
    max-width: 400px;
    margin: 100px auto;
    position: relative;
    flex: 1;
  }
  
  .spread img {
    /* max-height: 400px; */
    /* max-width: 400px; */
    width:50%;
    max-width:400px;
    /* margin:20px;  */
    transition: .3s;
    position: relative;
    filter:grayscale(30%);
    /* opacity:.8; */
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  }
  
    .spread img:hover {
      /* left: 50%; */
      /* top: 50%; */
      /* width:100%; */
      /* transform:translate(-50%, -50%); */
      transform: rotate(0deg);
      transform:scale(1.15);
     box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.3);
     filter:grayscale(0);

      /* position:absolute; */
      /* z-index:999; */
      /* opacity:1; */
      transition: 0.3s;
    }

  .spread :nth-child(2) {
    /* left: -10px; */
    transform: rotate(-2deg)
  }
  
  .spread :nth-child(3) {
    /* left: -20px; */
    /* top: 10px; */
    transform: rotate(2deg)
  }
  
  .spread :nth-child(4) {
    /* left: 40px; */
    transform: rotate(1deg);
    /* top:-20px; */
  }
  
  .spread :nth-child(5) {
    transform:rotate(2deg);
  }
  
  .spread :nth-child(6) {
    transform:rotate(-2deg);
  }

  .spread :nth-child(7) {
    transform:rotate(4deg);
    /* top:-10vw; */
  }

  
  .spread :nth-child(8) {
    transform:rotate(0deg);
  }
  
  .spread :nth-child(9) {
    transform:rotate(1deg);
    /* top:-9vw */
  }
  
  .spread :nth-child(10) {
    transform:rotate(4deg);
  }
  
  .spread :nth-child(11) {
    /* top:-20vw; */
    transform:rotate(-3deg);
  }
  
  .spread :nth-child(12) {
    transform:rotate(-2deg);
    /* top:-10vw; */
  }
  
  .spread :nth-child(13) {
    transform:rotate(1deg);
  }
  
  .spread :nth-child(14) {
    transform:rotate(2deg);
    /* top:-10vw; */
  }
  
  .spread :nth-child(15) {
    transform:rotate(2deg);
  }
  
  .spread :nth-child(16) {
    transform:rotate(0deg);
  }
  
  .spread :nth-child(17) {
    transform:rotate(2deg);
  }
  
  .spread :nth-child(18) {
    transform:rotate(2deg);
  }
  
  .spread :nth-child(19) {
    transform:rotate(2deg);
  }
  
  .spread :nth-child(20) {
    transform:rotate(2deg);
  }
  
  .spread :nth-child(21) {
    transform:rotate(2deg);
  }
  
  .spread img:hover~.spread img {
    border: solid 4px blue;
  }
  
  
@media only screen and (max-width:800px) {
    .spread,.spread {
        margin:25px;   
    }
    .spread h3,.spread h3 {
      font-size:1.5em;
    }
}
@media only screen and (max-width:500px) {
    .spread img,.spread img {
      max-width:300px;
      max-height:300px;
}
}